import React, { useRef, useState, useEffect } from "react";
import { Box } from "@mui/material";
import PictureCapture from "../../nonview/core/PictureCapture";
import PlantPhoto from "../../nonview/core/PlantPhoto";
import MenuButton from "../atoms/MenuButton";
import WelcomeSection from "../atoms/WelcomeSection";
import CameraView from "../atoms/CameraView";
import LoadingView from "../atoms/LoadingView";
import PlantPhotoView from "./PlantPhotoView";
import CameraControls from "../atoms/CameraControls";

const PictureCaptureView = () => {
  const videoRef = useRef(null);
  const canvasRef = useRef(null);
  const [isCameraActive, setIsCameraActive] = useState(false);
  const [stream, setStream] = useState(null);
  const [capturedImage, setCapturedImage] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [plantPhoto, setPlantPhoto] = useState(null);
  const [error, setError] = useState(null);
  const [isStoring, setIsStoring] = useState(false);
  const [blobUrl, setBlobUrl] = useState(null);
  const pictureCapture = useRef(new PictureCapture());

  // Cleanup on unmount
  useEffect(() => {
    const capture = pictureCapture.current;
    return () => {
      capture.cleanup();
    };
  }, []);

  // Attach stream to video element when camera becomes active
  useEffect(() => {
    if (isCameraActive && videoRef.current && stream) {
      videoRef.current.srcObject = stream;
    }
  }, [isCameraActive, stream]);

  // Update document title when plant is identified
  useEffect(() => {
    if (plantPhoto?.plantNetPredictions?.length > 0) {
      const topResult = plantPhoto.plantNetPredictions[0];
      document.title = topResult.species || "Vanam";
    } else {
      document.title = "Vanam";
    }
  }, [plantPhoto]);

  const startCamera = async () => {
    setIsLoading(true);
    try {
      const result = await pictureCapture.current.startCamera();
      if (result.success) {
        setStream(result.stream);
        setIsCameraActive(true);
      } else {
        alert(result.error);
      }
    } finally {
      setIsLoading(false);
    }
  };

  const stopCamera = () => {
    pictureCapture.current.stopCamera();
    setStream(null);
    setIsCameraActive(false);
  };

  const capturePhoto = async () => {
    const result = pictureCapture.current.capturePhoto(
      videoRef.current,
      canvasRef.current,
    );

    if (result.success) {
      setCapturedImage(result.imageData);
      setStream(null);
      setIsCameraActive(false);
      await identifyPlantFromImage(result.imageData);
    }
  };

  const clearImage = () => {
    setCapturedImage(null);
    setPlantPhoto(null);
    setError(null);
    setBlobUrl(null);
  };

  const uploadPhoto = async (file) => {
    setIsLoading(true);
    setError(null);
    setPlantPhoto(null);
    try {
      const result = await pictureCapture.current.loadFromFile(file);
      if (result.success) {
        setCapturedImage(result.imageData);
        await identifyPlantFromImage(result.imageData);
      } else {
        setError(result.error);
        setIsLoading(false);
      }
    } catch (err) {
      setError("Failed to load uploaded file");
      console.error(err);
      setIsLoading(false);
    }
  };

  const loadTestImage = async () => {
    setIsLoading(true);
    setError(null);
    setPlantPhoto(null);
    try {
      const result = await pictureCapture.current.loadTestImage();
      if (result.success) {
        setCapturedImage(result.imageData);
        await identifyPlantFromImage(result.imageData);
      } else {
        setError(result.error);
        setIsLoading(false);
      }
    } catch (err) {
      setError("Failed to load test image");
      console.error(err);
      setIsLoading(false);
    }
  };

  const identifyPlantFromImage = async (imageData) => {
    setIsLoading(true);
    try {
      const photo = await PlantPhoto.fromImage(imageData);
      setPlantPhoto(photo);
      storeResultsToBlob(photo);
    } catch (err) {
      setError(err.message || "Failed to identify plant");
      console.error(err);
    }
    setIsLoading(false);
  };

  const generateStorageKey = (plantPhoto) => {
    return `blob_stored_${plantPhoto.imageHash.substring(0, 16)}`;
  };

  const isAlreadyStored = (storageKey) => {
    try {
      const storedData = localStorage.getItem(storageKey);
      if (storedData && storedData !== "true") {
        // Return the stored URL if it exists
        return storedData;
      }
      return storedData === "true" ? true : false;
    } catch (error) {
      console.error("Error checking storage cache:", error);
      return false;
    }
  };

  const markAsStored = (storageKey, url) => {
    try {
      // Store the URL instead of just "true"
      localStorage.setItem(storageKey, url);
    } catch (error) {
      console.error("Error marking as stored:", error);
    }
  };

  const storeResultsToBlob = async (plantPhoto) => {
    const storageKey = generateStorageKey(plantPhoto);
    const cachedUrl = isAlreadyStored(storageKey);
    if (cachedUrl) {
      console.log(
        "Results already stored to Vercel Blob, skipping duplicate storage",
      );
      setBlobUrl(cachedUrl);
      return;
    }

    setIsStoring(true);
    try {
      const dataToStore = {
        timestamp: plantPhoto.utImageTaken,
        gpsData: plantPhoto.imageLocation
          ? {
              latitude: plantPhoto.imageLocation.latitude,
              longitude: plantPhoto.imageLocation.longitude,
              accuracy: plantPhoto.imageLocation.accuracy,
            }
          : null,
        results: plantPhoto.plantNetPredictions.map((p) => ({
          score: p.confidence,
          species: p.species,
          genus: p.genus,
          family: p.family,
          commonNames: p.commonNames,
          gbif_id: p.gbifId,
          powo_id: p.powoId,
          iucn_id: p.iucnId,
          iucn_category: p.iucnCategory,
        })),
      };

      const response = await fetch(
        "https://vanam-teal.vercel.app/api/store-results",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(dataToStore),
        },
      );

      const contentType = response.headers.get("content-type");

      if (!response.ok) {
        const errorText = await response.text();
        console.error(
          "Failed to store results. Status:",
          response.status,
          "Response:",
          errorText,
        );
        return;
      }

      // Check if response is actually JSON
      if (!contentType || !contentType.includes("application/json")) {
        const text = await response.text();
        console.error("Non-JSON response received:", text);
        return;
      }

      const result = await response.json();
      if (result.success) {
        // Mark as stored to prevent duplicates and store the URL
        markAsStored(storageKey, result.url);
        setBlobUrl(result.url);
      } else {
        console.error("Failed to store results:", result.error);
      }
    } catch (error) {
      console.error("Error storing results to blob:", error);
    } finally {
      setIsStoring(false);
    }
  };

  return (
    <Box sx={{ maxWidth: 600, mx: "auto", pb: 10, position: "relative" }}>
      <MenuButton />
      {!capturedImage ? (
        <Box>
          {isLoading && !isCameraActive ? (
            <LoadingView message="Identifying plant..." />
          ) : isCameraActive ? (
            <CameraView
              videoRef={videoRef}
              canvasRef={canvasRef}
              onCapture={capturePhoto}
              onCancel={stopCamera}
            />
          ) : (
            <Box sx={{ py: 4 }}>
              <WelcomeSection />
              <CameraControls
                onStartCamera={startCamera}
                onUploadPhoto={uploadPhoto}
                onLoadTestImage={loadTestImage}
                isLoading={isLoading}
              />
            </Box>
          )}
        </Box>
      ) : (
        <Box>
          <PlantPhotoView
            plantPhoto={plantPhoto}
            imageData={capturedImage}
            isLoading={isLoading}
            isStoring={isStoring}
            blobUrl={blobUrl}
            error={error}
          />

          <CameraControls
            onStartCamera={() => {
              clearImage();
              setIsCameraActive(false);
              startCamera();
            }}
            onUploadPhoto={(file) => {
              clearImage();
              uploadPhoto(file);
            }}
            onLoadTestImage={() => {
              clearImage();
              loadTestImage();
            }}
            isLoading={isLoading}
          />
        </Box>
      )}
    </Box>
  );
};

export default PictureCaptureView;
